.App {
  text-align: center;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.ContactForm {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 30rem;
  width: 40rem;
  margin: 100px auto;
  background-color: darkgrey;
}

.ui.form {
  justify-content: center;
  align-items: center;
  height: 30rem;
  width: 40rem;
}

.required.field {
  margin: 5px;
  padding: 10px
}

.ui.left.icon.input {
  padding: 10px;
}

label {
  padding: 10px;
}

textarea {
  width: 530px;
  height: 100px;
  padding: 10px;
  border-radius: 10px;
}

.videoTag {
  position: fixed;
  min-height: 100%;
  min-width: 100%;
  z-index: -1;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
}

.content-container {
  min-height: 100vh;
}

.About-me {
  position: relative;
  margin: 4rem;
  background-color: darkgray;
  padding: 10px;
  border-radius: 10px;
  border-color: black;
  border-width: 5px;
  border-style: groove;
  opacity: 0.95;
}

.About-me h2 {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 4rem;
  text-align: center;
  text-decoration: double;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.myPhoto {
  height: 350px;
  width: 350px;
  border-radius: 50%;
  margin: 20px;
}

.about-text {
  padding: 15px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 22px;

}

.stock-img {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}


.Projects {
  overflow-y: scroll;
  position: relative;
  margin-bottom: 15px 10px 100px 10px;
}

.footer--pin {
  display: grid;
  width: 100%;
  height: 95px;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: darkgrey;
  opacity: 0.9;
  align-content: center;

}

div.icon-container {
  display: flex;
  justify-content: space-evenly;
}

svg {
  height: 4rem;
  width: 4rem;
  color: dodgerblue;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .about-text {
    font-size: 17px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
  }

  .myPhoto {
    height: 200px;
    width: 200px;
    opacity: 1;
    text-align: center;
  }

  .About-me {
    margin: 25px;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 279px) {
  .about-text {
    padding: 15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 20px;
    width: 500px;
    text-align: center;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .about-text {
    padding: 15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 22px;
    width: 700px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .about-text {
    padding: 15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 22px;
    min-width: 500px;
  }
}

@media only screen and (min-width: 1310px) {
  .about-text {
    text-align: justify;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1310px) {
  .about-text {
    padding: 15px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 22px;

  }
}

@media only screen and (min-width: 1709px) {
  .about-text {
    padding: 15px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 22px;
    width: 1100px;
  }
}

@media only screen and (max-width: 625px) {
  .ContactForm {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 34rem;
    width: 20rem;
    margin: 90px auto;
    background-color: darkgrey;
  }

  .ui.form {
    justify-content: center;
    align-items: center;

  }

  .required.field {
    margin: 5px;
    padding: 5px
  }

  .ui.left.icon.input {
    padding: 5px;
  }

  label {
    padding: 10px;
  }

  textarea {
    width: 200px;
    height: 100px;
    padding: 10px;
    border-radius: 10px;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}