#root {
  height: 100%
}

.card {
  background-color: darkgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  padding: 25px;
  border-radius: 25px;
  border-color: darkcyan;
  border-width: 5px;
  border-style: groove;
  opacity: 0.89;
  height: 400px;
  width: 600px;
}

.card-img-top {
  height: 300px;
  width: 550px;
  border-radius: 25px;
  z-index: -1;
}

.Projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 100px;
}

.card-title {
  font-size: 2rem;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.card-text {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

@media (max-width: 350px) {
  .card {
    height: 250px;
    width: 200px;
  }

  .card-img-top {
    height: 141px;
    width: 235px;
  }
}

@media (min-width: 350px) and (max-width: 760px) {
  .card {
    height: 350px;
    width: 300px;
  }

  .card-img-top {
    height: 241px;
    width: 300px;
  }
}